import React from 'react';
import ModalBuyPackage from './ModalBuyPackage';

function PackageItem(props) {
    const [modalShow, setModalShow] = React.useState(false);

    return (
        <>
            <div className="col-xl-4 col-xxl-6 col-lg-6">
                <div className="card">
                    <div className="card-header  border-0 pb-0">
                        <div className='row d-flex justify-content-center'>
                            <div className='col-12 d-flex justify-content-center'>
                                <h4 className={props.package.type == 'vip3' ? "card-title text-success" : "card-title text-warning"}>
                                    {(props.type == 'VCB' && "Gi " + props.package.name + " Ngn Hng Vietcombank") || (props.type == 'TCB' && "Gi " + props.package.name + " Ngn Hng Techcombank") || (props.type == 'MB' && "Gi " + props.package.name + " Ngn Hng MB-Bank")}
                                </h4>
                            </div>
                            <div className='col-12 d-flex justify-content-center'>
                                {props.package.type == 'vip1' && <img src="/images/vip1.png" width={100} height={100} />}
                                {props.package.type == 'vip2' && <img src="/images/vip 2.png" width={100} height={100} />}
                                {props.package.type == 'vip3' && <img src="/images/vip3.png" width={100} height={100} />}
                                
                            </div>
                            <div className='col-12 d-flex justify-content-center'>
                                <h1 className="card-title text-primary">{props.package.price_fm} VN</h1>
                            </div>
                        </div>
                    </div>
                    <div className="card-body">
                        <div
                            id="DZ_W_Todo1"
                            className="widget-media dz-scroll ps ps--active-y"
                        >
                            <div className='row'>
                                <div className='col-12 pb-4'><strong className='text-warning'>Quyn li:</strong></div>
                            </div>
                            <div className='row pb-3'>
                                <div className='col-12'>
                                    <strong className=''>- M ti a {props.package.max_create_account} ti khon</strong>
                                </div>
                            </div>
                            <div className='row pb-3'>
                                <div className='col-12'>
                                    <strong className=''>- {props.package.max_edit_account} ln sa thay i s ti khon</strong>
                                </div>
                            </div>
                            <div className='row pb-3'>
                                <div className='col-12'>
                                    <strong className='text-primary'>- Check ti khon khng gii hn</strong>
                                </div>
                            </div>
                            <div className='row pb-3'>
                                <div className='col-12'>
                                    {props.package.max_transfer_free == -1 ? <strong className='text-primary'>- Chuyn khon khng gii hn</strong> : <strong className=''>- Chuyn khon {props.package.max_transfer_free} ln / ngy</strong>}
                                    
                                </div>
                            </div>
                            <div className='row pb-3'>
                                <div className='col-12'>
                                    {props.package.max_deposit_app == -1 ? <strong className='text-primary'>- Np vo app khng gii hn </strong> :<strong className=''>- Np ti a {props.package.max_deposit_app_fm} VN vo app</strong>}
                                </div>
                            </div>
                            <div className='row pb-3'>
                                <div className='col-12'>
                                    <strong className=''>( Chit khu 80% np tin vo app )</strong>
                                </div>
                            </div>
                        </div>
                        <div className='row'>
                            <div className='col-12 d-flex justify-content-center'>
                                <button className="btn btn-primary sw-btn-next" onClick={() => setModalShow(true)} type="button">
                                    ng k gi
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <ModalBuyPackage
                 show={modalShow}
                 onHide={() => setModalShow(false)}
                 packageName={props.package.name}
                 packagePrice={props.package.price_fm}
                 type={props.type}
                 typePackage={props.package.type}
                 bankCode={props.package.bank_code}
            />
        </>
    );
}

export default PackageItem;