import React, { useEffect, useState } from 'react';
import ThemeBreadcrumb from '../themes/ThemeBreadcrumb';
import MasterLayout from '../../layouts/MasterLayout';
import ImageBill from './ImageBill';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import CurrencyInput from '../global/CurrencyInput';
import AppNotification from '../../helpers/AppNotification';
import { useDispatch } from 'react-redux';
import VietQrModel from '../../models/VietQrModel';
import { setDownloadBillData, setShowPreloader } from '../../redux/action';
import UserModel from '../../models/UserModel';
import Swal from 'sweetalert2';
import { Link, useNavigate, useParams } from 'react-router-dom';
const validationSchema = Yup.object().shape({
    amount: Yup.string().required('S tin khng c  trng'),
    hour: Yup.string().required('Gi khng c  trng'),
    day: Yup.string().required('Ngy khng c  trng'),
    receiver_name: Yup.string().required('Tn ngi nhn khng c  trng'),
    receiver_number: Yup.string().required('S in thoi ngi nhn khng c  trng'),
    receiver_bank_name: Yup.string().required('Tn ngn hng ngi nhn khng c  trng'),
    transaction_code: Yup.string().required('M giao dch khng c  trng'),
    operatingSystem: Yup.string().required('H iu hnh khng c  trng'),
    decreption: Yup.string()
    .required('M t khng c  trng')
    .max(150, 'M t khng c vt qu 150 k t'),
  });


function FormCreateBill(props) {
    // x l m giao dch VCB
    const generateRandomNumber = () => {
        const fixedDigits = "555";
        const randomDigits = Math.floor(Math.random() * 10000000).toString().padStart(7, "0");
        const result = fixedDigits + randomDigits;
        return result;
      }
    const { type } = useParams();
    if (type == "VCB") {
        var tranansaction_code_random = generateRandomNumber();
    }

    // x l m giao dch TPB
    const generateRandomString = () => {
        const fixedString = '661V00923282';
        const randomLetters = generateRandomLetters(4); // To 4 ch ci ting Anh ngu nhin
      
        return fixedString + randomLetters;
      }
      
    const generateRandomLetters = (length) => {
        const letters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
        let randomString = '';
      
        for (let i = 0; i < length; i++) {
          const randomIndex = Math.floor(Math.random() * letters.length);
          randomString += letters[randomIndex];
        }
      
        return randomString;
      }
    if (type == "TPB") {
        var tranansaction_code_random = generateRandomString();
    }

    // x l m giao dch TCB

    const generateRandomStringTCB = () => {
        const fixedString = 'FT2408';
        const randomDigits = generateRandomDigitsTCB(9); // To 9 ch s ngu nhin
      
        return fixedString + randomDigits;
      }
      
      const generateRandomDigitsTCB = (length) => {
        let randomString = '';
      
        for (let i = 0; i < length; i++) {
          const randomDigit = Math.floor(Math.random() * 10);
          randomString += randomDigit;
        }
      
        return randomString;
      }

      if (type == "TCB") {
        var tranansaction_code_random = generateRandomStringTCB();
    }


    const [initialValues, setInitialValues] = useState({
        type: type,
        amount: '',
        hour: '',
        day: '',
        receiver_name: '',
        receiver_number: '',
        receiver_bank_name: '',
        transaction_code: tranansaction_code_random ? tranansaction_code_random : '',
        operatingSystem: '',
        decreption: '',
        sender_name: '',
        sender_number: '',
    });

    useEffect(() => {
        const currentDate = new Date();
        const currentHour = currentDate.getHours().toString().padStart(2, "0"); // Ly gi hin ti v nh dng thnh chui 2 ch s
        const currentDay = currentDate.toISOString().split("T")[0]; // Ly ngy hin ti v nh dng thnh chui YYYY-MM-DD
    
        setInitialValues({
          ...initialValues,
          hour: currentHour,
          day: currentDay
        });
      }, []);
    
    const navigate = useNavigate();
    // const { typeBank, bankName } = location.state;
    // console.log(typeBank);
    const dispatch = useDispatch();
    const [flagShowImage, setFlagShowImage] = useState(false);

    const [imageTemplate, setImageTemplate] = useState('/bills/' + type + '/base.png');
    const [exportBill, setExportBill] = useState(false);

    const [operating, setOperating] = useState();
    useEffect(() => {
        setOperating(0); // Set li gi tr ca showFormCreateBill thnh 0 mi khi component c ti li
    }, []);
    const handleChangeOperating = (event) => {
        setOperating(event.target.value);
    };

    const [vietQrBanks, setVietQrBanks] = useState([]);

    useEffect(() => {
        dispatch(setShowPreloader(true));
        VietQrModel.getAllVietQR({})
            .then(res => {
                dispatch(setShowPreloader(false));
                setVietQrBanks(res.data);
            }).catch(err => {
                dispatch(setShowPreloader(false));
                console.log(err);
                AppNotification.showError('Ly d liu tht bi!');
            })
    }, []);

    

    // switch (type) {
        //     case 'MB':
        //     case 'ACB':
        //     case 'ICB':
        //     case 'TPB':
        //         setInitialValues({
        //             type: type,
        //             amount: '',
        //             hour: '',
        //             day: '',
        //             receiver_name: '',
        //             receiver_number: '',
        //             receiver_bank_name: '',
        //             transaction_code: '',
        //             operatingSystem: '',
        //             decreption: '',
        //             sender_name: '',
        //             sender_number: '',
        //         });
        //         break;
        //         case 'MSB':
        //         setInitialValues({
        //             type: type,
        //             amount: '',
        //             hour: '',
        //             day: '',
        //             receiver_name: '',
        //             receiver_number: '',
        //             receiver_bank_name: '',
        //             transaction_code: '',
        //             operatingSystem: '',
        //             decreption: '',
        //             sender_name: '',
        //         });
        //         break;
        //         case 'VCB':
        //             setInitialValues({
        //                 type: type,
        //                 amount: '',
        //                 hour: '',
        //                 day: '',
        //                 receiver_name: '',
        //                 receiver_number: '',
        //                 receiver_bank_name: '',
        //                 transaction_code: generateRandomNumber(),
        //                 operatingSystem: '',
        //                 decreption: '',
        //             });
        //         break;
        //     default:
        //         setInitialValues({
        //             type: type,
        //             amount: '',
        //             hour: '',
        //             day: '',
        //             receiver_name: '',
        //             receiver_number: '',
        //             receiver_bank_name: '',
        //             transaction_code: '',
        //             operatingSystem: '',
        //             decreption: '',
        //         });
        // }


    const handleSubmit = (values) => {
        console.log(values);
        setExportBill(false);
        setInitialValues({
            ...values,
            type: type
        });
        setFlagShowImage(true);

    };

    useEffect(() => {
        if (flagShowImage == true) {
            dispatch(setDownloadBillData(initialValues));
            navigate('/dowload-bill');
        }
    }, [initialValues,flagShowImage]);

    const handleExportBill = () => {
        dispatch(setShowPreloader(true));
        UserModel.feeDowloadBill()
            .then(res => {
                dispatch(setShowPreloader(false));
                if (res.success == true) {
                    setExportBill(true);
                } else {
                    Swal.fire({
                        icon: "error",
                        title: res.messange,
                        showConfirmButton: false,
                        timer: 2000,
                    });
                }
            }).catch(err => {
                dispatch(setShowPreloader(false));
                console.log(err);
                AppNotification.showError('Ly d liu tht bi!');
            })
    };

    const [infoBankBill, setInfoBankBill] = useState({});

    useEffect(() => {
        let foundElement = null;
        vietQrBanks.forEach(element => {
            if (element.code === type) {
                foundElement = element;
            }
        });

        if (foundElement) {
            setInfoBankBill(foundElement);
        }
    }, [type, vietQrBanks]);
    return (
        <MasterLayout>
            <ThemeBreadcrumb title={`To bill / Fake Bill Chuyn Khon ${infoBankBill.shortName}`} />
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='row'>
                        <div className="col-xl-8 col-lg-8">
                            <div className="row">
                                <div className="col-xl-12 col-xxl-12">
                                    <div className="card">
                                        <div className="card-header">
                                            <h4 className="card-title">Danh Sch Ngn Hng Fake Bill Chuyn Khon {infoBankBill.shortName}</h4>
                                        </div>
                                        <div className="card-body">
                                            <Formik
                                                initialValues={initialValues}
                                                validationSchema={validationSchema}
                                                onSubmit={handleSubmit}
                                            >
                                                {({ errors, touched, setFieldValue }) => (

                                                    <Form>
                                                        <div id="smartwizard" className="form-wizard order-create sw sw-theme-default sw-justified">
                                                            <div className="tab-content" style={{ height: 'auto' }}>
                                                                <div
                                                                    id="wizard_Service"
                                                                    className="tab-pane"
                                                                    role="tabpanel"
                                                                    style={{ display: 'block' }}
                                                                >
                                                                    <div className="row">
                                                                        <div className="col-lg-6 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">S tin*</label>
                                                                                <CurrencyInput
                                                                                    type="text"
                                                                                    className={`form-control ${errors.amount && touched.amount ? 'is-invalid' : ''
                                                                                        }`}
                                                                                    autoFocus
                                                                                    onChange={e => {
                                                                                        let value = e.target.value || "";
                                                                                        value = value.replaceAll(',', '');
                                                                                        setFieldValue("amount", value);
                                                                                    }}
                                                                                />
                                                                                <ErrorMessage name="amount" component="div" className="error-message text-danger" />
                                                                                <Field
                                                                                    type="hidden"
                                                                                    name="amount"
                                                                                    className={`form-control ${errors.amount && touched.amount ? 'is-invalid' : ''
                                                                                        }`}
                                                                                    autoFocus
                                                                                    placeholder=""
                                                                                    required=""
                                                                                    fdprocessedid="y2s2mp"
                                                                                />
                                                                            </div>
                                                                        </div>
                                                                        <div className="col-lg-6 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">Gi*</label>
                                                                                <Field
                                                                                    type="time"
                                                                                    name="hour"
                                                                                    className="form-control"
                                                                                    placeholder=""
                                                                                    required=""
                                                                                    fdprocessedid="4aq0kr"
                                                                                />
                                                                                <ErrorMessage name="hour" component="div" className="error-message text-danger" />
                                                                            </div>
                                                                        </div>
                                                                        <div className="col-lg-6 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">Ngy*</label>
                                                                                <Field
                                                                                    type="date"
                                                                                    name="day"
                                                                                    className="form-control"
                                                                                    placeholder=""
                                                                                    required=""
                                                                                    fdprocessedid="4aq0kr"
                                                                                />
                                                                                <ErrorMessage name="day" component="div" className="error-message text-danger" />
                                                                            </div>
                                                                        </div>
                                                                        <div className="col-lg-6 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">Tn ngi nhn*</label>
                                                                                <Field
                                                                                    type="text"
                                                                                    name="receiver_name"
                                                                                    className="form-control"
                                                                                    placeholder=""
                                                                                    required=""
                                                                                    fdprocessedid="4aq0kr"
                                                                                />
                                                                                <ErrorMessage name="receiver_name" component="div" className="error-message text-danger" />
                                                                            </div>
                                                                        </div>
                                                                        <div className="col-lg-6 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">S ti khon ngi nhn*</label>
                                                                                <Field
                                                                                    type="text"
                                                                                    name="receiver_number"
                                                                                    className="form-control"
                                                                                    placeholder=""
                                                                                    required=""
                                                                                    fdprocessedid="4aq0kr"
                                                                                />
                                                                                <ErrorMessage name="receiver_number" component="div" className="error-message text-danger" />
                                                                            </div>
                                                                        </div>
                                                                        {
                                                                            (type == 'MB' ||
                                                                                type == 'ACB ' ||
                                                                                type == 'ICB' ||
                                                                                type == 'TPB')
                                                                                ?
                                                                                (
                                                                                    <>
                                                                                        <div className="col-lg-6 mb-2">
                                                                                            <div className="mb-3">
                                                                                                <label className="text-label form-label">S ti khon ngi gi*</label>
                                                                                                <Field
                                                                                                    type="text"
                                                                                                    name="sender_number"
                                                                                                    className="form-control"
                                                                                                    placeholder=""
                                                                                                    required=""
                                                                                                    fdprocessedid="4aq0kr"
                                                                                                />
                                                                                                <ErrorMessage name="sender_number" component="div" className="error-message text-danger" />
                                                                                            </div>
                                                                                        </div>
                                                                                    </>
                                                                                )
                                                                                :
                                                                                ''
                                                                        }
                                                                        {
                                                                            (type == 'MB' ||
                                                                                type == 'ACB ' ||
                                                                                type == 'ICB' ||
                                                                                type == 'TPB' ||
                                                                                type == 'MSB')
                                                                                ?
                                                                                (
                                                                                    <>
                                                                                        <div className="col-lg-6 mb-2">
                                                                                            <div className="mb-3">
                                                                                                <label className="text-label form-label">Tn ngi gi*</label>
                                                                                                <Field
                                                                                                    type="text"
                                                                                                    name="sender_name"
                                                                                                    className="form-control"
                                                                                                    placeholder=""
                                                                                                    required=""
                                                                                                    fdprocessedid="4aq0kr"
                                                                                                />
                                                                                                <ErrorMessage name="sender_name" component="div" className="error-message text-danger" />
                                                                                            </div>
                                                                                        </div>
                                                                                    </>
                                                                                )
                                                                                :
                                                                                ''
                                                                        }
                                                                        <div className="col-lg-6 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">Ngn hng th hng*</label>
                                                                                <Field
                                                                                    as="select"
                                                                                    type="text"
                                                                                    name="receiver_bank_name"
                                                                                    className="form-control"
                                                                                    placeholder=""
                                                                                    required=""
                                                                                    fdprocessedid="4aq0kr"
                                                                                >
                                                                                    <option className='option bg-primary' value="">- -Chn Ngn hng- -</option>
                                                                                    {
                                                                                        vietQrBanks.map((item, key) => (
                                                                                            <option className='option bg-primary' value={item.name}>{item.name}</option>
                                                                                        ))
                                                                                    }
                                                                                </Field>
                                                                                <ErrorMessage name="receiver_bank_name" component="div" className="error-message text-danger" />
                                                                            </div>
                                                                        </div>
                                                                        <div className="col-lg-6 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">M giao dch*</label>
                                                                                <Field
                                                                                    type="text"
                                                                                    name="transaction_code"
                                                                                    className="form-control"
                                                                                    placeholder=""
                                                                                    required=""
                                                                                    fdprocessedid="4aq0kr"
                                                                                    value={initialValues.transaction_code}
                                                                                />
                                                                                <ErrorMessage name="transaction_code" component="div" className="error-message text-danger" />
                                                                            </div>
                                                                        </div>
                                                                        <div className="col-lg-6 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">H iu hnh*</label>
                                                                                <Field
                                                                                    as="select"
                                                                                    name="operatingSystem"
                                                                                    className="form-control"
                                                                                    required=""
                                                                                    fdprocessedid="4aq0kr"
                                                                                >
                                                                                    <option className='option bg-primary' value="">- - chn h iu hnh - -</option>
                                                                                    <option className='option bg-primary' value="android">Android</option>
                                                                                    <option className='option bg-primary' value="ios">IOS</option>
                                                                                </Field>
                                                                                <ErrorMessage name="operatingSystem" component="div" className="error-message text-danger" />
                                                                            </div>
                                                                        </div>
                                                                        <div className="col-lg-12 mb-2">
                                                                            <div className="mb-3">
                                                                                <label className="text-label form-label">Ni dung giao dch*</label>
                                                                                <Field
                                                                                    type="text"
                                                                                    name="decreption"
                                                                                    className="form-control"
                                                                                    placeholder=""
                                                                                    required=""
                                                                                    fdprocessedid="4aq0kr"
                                                                                />
                                                                                <ErrorMessage name="decreption" component="div" className="error-message text-danger" />
                                                                            </div>
                                                                        </div>
                                                                        {/* Thm cc trng khc tng t */}
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div
                                                                className="toolbar toolbar-bottom d-flex justify-content-between"
                                                                role="toolbar"
                                                                style={{ textAlign: 'right' }}
                                                            >
                                                                <Link
                                                                    className="btn btn-primary sw-btn-prev"
                                                                    to="/bill"
                                                                >
                                                                    Quay li
                                                                </Link>
                                                                <button type="submit" className="btn btn-primary ms-2 sw-btn-prev">
                                                                    To ha n
                                                                </button>
                                                            </div>
                                                        </div>
                                                    </Form>
                                                )}

                                            </Formik>
                                            <div className='row pt-3'>
                                                <div className='col'>
                                                    <strong className='text-warning'>*Lu  :</strong>
                                                    <p className='text-primary'>- Ph to bill l : 19.000 VN</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        {/* <ImageBill exportBill={exportBill} imageBill={imageTemplate} initialValues={initialValues} flagShowImage={flagShowImage} /> */}
                        <ImageBill imageBill={imageTemplate} />
                    </div>
                </div>
            </div>
        </MasterLayout>
    );
}

export default FormCreateBill;