import React, { useState } from 'react';

function InstructEarnmonye(props) {
    const [isShow, setIsShow] = useState(0);
    const handleShow = () => {
        setIsShow(1);
    }
    const handleClose = () => {
        setIsShow(0);
    }
    return (
        <>
            <div className="row">
                <div className="col-12">
                    <p><span className="text-warning">* Rt tin :</span></p><br />
                    <p><span className='text-primary'>- Ch c th rt tin t ti khon gii thiu :</span> s tin ny kim c khi cc ti khon gii thiu np tin vo web, hoa hng 10%/ tng s tin np ca ti khon gii thiu.</p>
                    <p><span className='text-primary'>- Bc 1 :</span> Nhn vo rt tin, s xut hin hp thoi rt tin.</p>
                    <p><span className='text-primary'>- Bc 2 :</span> Nhp thng tin ti khon nhn tin gm : ch ti khon v s ti khon.</p>
                    <p><span className='text-primary'>- Bc 3 :</span> Nhp s tin mun rt.</p>
                    <p><span className='text-primary'>- Bc 4 :</span> Nhn vo "Ly m"  ly m OTP rt tin, m OTP s c gi vo email ca qu khch.</p>
                    <p><span className='text-primary'>- Bc 5 :</span> Nhp m OTP v nhn nt "xc nhn"  rt tin...</p>
                </div>
            </div>
            {
                isShow == 1 ?
                    <div>
                        <div className='row'>
                            <div className='col-12'>
                                <p><span className='text-primary'>- Bc 6 :</span> ch qun tr vin thc hin xc nhn v gi tin vo ti khon qu khch va nhp trong t pht!.</p>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12">
                                <p><span className="text-warning">* Chuyn tin :</span></p><br />
                                <p><span className='text-primary'>- Ch c th chuyn tin t ti khon gii thiu vo ti khon chnh :</span> s tin ny kim c khi cc ti khon gii thiu np tin vo web, hoa hng 10%/ tng s tin np ca ti khon gii thiu.</p>
                                <p><span className='text-primary'>- Bc 1 :</span> Nhn vo chuyn tin, s xut hin hp thoi chuyn tin.</p>
                                <p><span className='text-primary'>- Bc 2 :</span> Nhp s tin mun chuyn.</p>
                                <p><span className='text-primary'>- Bc 3 :</span> Nhn vo "Ly m"  ly m OTP chuyn tin, m OTP s c gi vo email ca qu khch.</p>
                                <p><span className='text-primary'>- Bc 4 :</span> Nhp m OTP v nhn nt "xc nhn"  chuyn tin...</p>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 d-flex justify-content-center">
                                <button onClick={handleClose} className='btn btn-primary btn-sm'>ng</button>
                            </div>
                        </div>
                    </div>
                    :
                    ''
            }
            {
                isShow == 0 ?
                    <div className="row">
                        <div className="col-12 d-flex justify-content-center">
                            <button onClick={handleShow} className='btn btn-primary btn-sm'>Xem thm</button>
                        </div>
                    </div>
                    : ''
            }
        </>

    );
}

export default InstructEarnmonye;