        // Thi gian chuyn
        ctx.font = '500 25px Inter';
        ctx.fillStyle = '#000000';
        ctx.textAlign = 'left';
        ctx.textBaseline = 'middle';
        ctx.fillText(data.hour + ' ' + this.formatThu(data.day) + ' ' + this.formatDate(data.day), canvas.width - 430, 823 + top);

