import React, { useState } from 'react';

function InstructBank(props) {
    const [isShow, setIsShow] = useState(0);
    const handleShow = () => {
        setIsShow(1);
    }
    const handleClose = () => {
        setIsShow(0);
    }
    return (
        <>
            <div className="row">
                <div className="col-12">
                    <p><span className="text-warning">* Mi ngn hng s c 2 s la chn l "Ti App" v "M Ngn Hng" :</span></p><br/>
                    <p><span className='text-primary'>- Nu chn "Ti App" :</span> th s m ra mt mn hnh bn phi cho chng ta la chn ti App cho Android hoc IOS</p>
                    <p><span className='text-primary'>- Nu chn "M Ngn Hng" :</span> th s m ra mt mn hnh bn phi gm nhng thng tin ngn hng v danh sch ti khon App</p>
                </div>
            </div>
            <div className="row">
                <div className="col-12">
                    <p><span className="text-warning">* Phn danh sch ti khon App :</span></p><br/>
                    <p><span className='text-primary'>- Nu cha c ti khon th bm vo thm ti khon :</span> Nhp cc thng tin cn thit, lu  stk ngn hng phi l s ti khon ng k  ngn hng .</p>
                    <p><span className='text-primary'></span>- Sau khi c ti khon th dng thng tin   ng nhp vo app...</p>
                </div>
            </div>
            {
                isShow == 1 ?
                    <div>
                        <div className="row">
                            <div className="col-12">
                                <p><span className='text-primary'>- Nhn vo biu tng hnh mt :</span> s hin th thng tin s d, mt khu v m 2FA </p>
                                <p><span className='text-primary'>- Nhn vo biu tng du cng :</span> s hin th chc nng np tin vo app,sa ti khon v xa ti khon </p>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 d-flex justify-content-center">
                                <button onClick={handleClose} className='btn btn-primary btn-sm'>ng</button>
                            </div>
                        </div>
                    </div>
                    :
                    ''
            }
            {
               isShow == 0 ? 
                    <div className="row">
                        <div className="col-12 d-flex justify-content-center">
                            <button onClick={handleShow} className='btn btn-primary btn-sm'>Xem thm</button>
                        </div>
                    </div>
                : ''
            }
        </>

    );
}

export default InstructBank;